:root{--white:#fff;--black:#000;--red:#ff4f5e;--light-red:#fdc6c8;--green:#b7d43f;--valid-green:#59cbb7;--near-black:#4a4a4a;--grey:#e7e5e2;--darker-grey:#e6e5e3;--light-grey:#f3f2f0;--lighter-grey:#f9f9f9;--desert-storm:#f3f2f1;--warm-grey:#959595;--blue:#629ff4;--link-color:var(--blue);--override-z-index:3;--top-z-index:2;--middle-z-index:1;--bottom-z-index:0;--base-font-family:"Open Sans",sans-serif;--strong-font-family:"Zilla Slab",Georgia,Utopia,Charter,serif;--font-size:16px;--font-size-xs:12px;--font-size-lg:24px;--font-size-xl:32px;--font-size-xxl:34px;--standard-margin:0.3rem;--wide-desktop-width:1400px;--desktop-width:76rem;--old-desktop-width:55rem;--page-margin:1rem;--input-height:2.5rem;--spinner-height:10rem;--card-shadow:rgba(0,0,0,0.09) 0px 1px 6px,rgba(0,0,0,0.09) 0px 1px 4px;--transition-duration:0.2s;--transition-duration-slow:0.5s;--easing:cubic-bezier(0.4,0,0.2,1);--header-height:4rem;--nav-shadow-overhang:0px 2px 4px rgba(0,0,0,0.03);--dataset-item-margin:0.7rem;--profile-column-margin:30px}@keyframes fade-in{0%{opacity:0;pointer-events:none}to{opacity:1;pointer-events:all}}.md-block{display:none}@media (min-width:768px){.md-block{display:block}}@media (max-width:768px){.hidden-sm-down{display:none!important}}@media (max-width:992px){.hidden-md-down{display:none!important}}@media (min-width:768px){.hidden-md-up{display:none!important}}@media (min-width:992px){.hidden-lg-up{display:none!important}}.stats-card{min-height:340px;box-shadow:0 5px 10px 0 rgba(0,0,0,.1);background:#fff;background:var(--white)}.stats-card .filters,.stats-card .title-and-icon{background:#f9f9f8}.stats-card .title-and-icon{padding:0 20px;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.stats-card .title-and-icon button{border:none}.stats-card .title-and-icon button:hover{opacity:.5}.stats-card .title-and-icon button path{fill:#959595;fill:var(--warm-grey)}.stats-card .title-and-icon button.active path{fill:#000;fill:var(--black)}[dir=ltr]