.fraction{display:flex;flex-direction:row}.fraction .numerator{line-height:1;font-size:48px}@media (--md-down){.fraction .numerator{font-size:24px}}.fraction .denominator{display:flex;flex-direction:row;flex-shrink:0;white-space:pre-wrap}.circle-progress,.circle-progress svg{width:68px;height:68px}.circle-progress circle{stroke:var(--grey)}.circle-progress .progress-circle{transform-origin:center;transform:rotate(-90deg);stroke:var(--type-color)}.circle-progress text{fill:var(--type-color);font-weight:600}